.projects {
  display: flex;
  flex-direction: column;
  gap: 48px;
  margin-top: 80px;
}
.projects .projects-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.projects .projects-title .projects-subtitle-description {
  max-width: 392px;
}
.projects .projects-title .projects-subtitle-description p {
  margin-top: 11px;
}
@media only screen and (max-width: 450px) {
  .projects .projects-title {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
  }
}
.projects .projects-subtitle-more {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  align-items: flex-end;
  align-content: flex-start;
  width: 100%;
  overflow: auto;
  flex-direction: row;
}
.projects .projects-subtitle-more .more {
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
  cursor: pointer;
}
.projects .projects-subtitle-more .more p {
  font-weight: 700;
}

.project-title {
  display: flex;
  justify-content: center;
}/*# sourceMappingURL=style.css.map */